/* Variable declarations */
:root {
    --mainBlue: #006699;
    --mainGreen: #339966;
    --darkGray: #403B41;
    --softGray: #DEDEDE;
    --serif: 'David Libre', serif;
    --sansSerif: 'Arimo', sans-serif;
}

* {
  box-sizing: border-box;
}

/* Overall styles - Starting points */
html {
    background-color: var(--softGray);
    color: var(--darkGray);
    font-family: var(--sansSerif);
    max-width: 100vw;
    max-height: 100vh;
}

body {
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--serif);
  margin: 0;
}

.section-title {
  padding: 2% 1% 4%;
  text-align: center;
  font-size: 3rem;
}

section {
  padding: 3% 0;
  max-width: 100vw;
}

/* Hero section */
.hero {
  margin-top: 25vh;
  min-height: 100vh;
}

.hero h1 {
  font-size: 4.5rem;
  text-align: center;
}

.break-line {
  width: 55vw;
  height: 1vh;
  background: linear-gradient(to right, var(--mainGreen), var(--mainBlue));
  border-radius: 5px;
  margin: 2% auto;
}

.intro-icons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.intro-icons img {
  width: 7vw;
  padding: 0 1.5em;
}

.intro-icons a {
  transition: background-color 0.8s;
}

.intro-icons a:hover {
  background-color: var(--mainGreen);
}

.intro-icons a:hover:nth-child(2) {
  background-color: #197f80;
}

.intro-icons a:hover:nth-child(3) {
  background-color: var(--mainBlue);
}

.more {
  margin: 2% auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.more p {
  font-size: 1.3rem;
  margin: 1% 0 0;
}

.more img {
  width: 4vw;
  margin-top: -1%;
}

/* Project Style */
.project-section {
  color: var(--softGray);
  background-color: var(--darkGray);
}

.project-title {
  text-decoration: underline var(--mainGreen);
}

.project-block {
  width: 85vw;
  height: 57vh;
  margin: 1% auto;
  padding: 1%;
  border: 3px solid var(--mainGreen);
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: 20% 50% 15% 15%;
  grid-column-gap: 1.5%;
  align-items: center;
}

.project-block:nth-child(even) {
  border: 3px solid var(--mainBlue);
}

.project-name {
  grid-column: 2;
  grid-row: 1;
  text-align: center;
  font-size: 2rem;
  text-decoration: underline var(--mainBlue);
}

.project-block:nth-child(even) .project-name {
  text-decoration: underline var(--mainGreen);
}

.project-images {
  grid-column: 1;
  grid-row: 1 / 5;
  align-self: stretch;
  justify-self: center;
  display: flex;
  flex-wrap: wrap;
}

.project-images img {
  max-width: 100%;
  max-height: 100%;
}

.multi-img img {
  max-width: 50%;
  max-height: 50%;
}

.project-desc {
  grid-column: 2;
  grid-row: 2;
  font-size: 1.25rem;
  padding: 0 2%;
}

.project-tech {
  grid-column: 2;
  grid-row: 3;
  color: var(--mainGreen);
  text-align: center;
}

.project-block:nth-child(even) .project-tech {
  color: var(--mainBlue);
}

.project-links {
  grid-column: 2;
  grid-row: 4;
  text-align: center;
}

.project-links a {
  color: var(--softGray);
  padding: 2%;
  font-size: 1.2rem;
  margin: 0 3%;
  text-decoration: underline var(--mainGreen);
  border: 1px solid var(--mainBlue);
  transition: background-color 0.8s;
}

.project-block:nth-child(even) .project-links a {
  text-decoration: underline var(--mainBlue);
  border: 1px solid var(--mainGreen);
}

.project-links a:hover {
  background-color: var(--mainBlue);
}

.project-block:nth-child(even) .project-links a:hover {
  background-color: var(--mainGreen);
}

/* Certificate Styles */
.certs {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: center;
  justify-content: space-around;
}

.cert-title {
  text-decoration: underline var(--mainBlue);
}

.cert-block {
  width: 40vw;
  border: 1.5px solid var(--darkGray);
  background-color: var(--darkGray);
  box-shadow: 7px 12px 20px -2px var(--darkGray);
}

.cert-block img {
  width: 100%;
  height: 100%;
}

.cert-caption {
  color: var(--softGray);
  text-align: center;
  padding: 2%;
}

/* About / Contact Styles */
.about-section {
  background-color: var(--darkGray);
  color: var(--softGray);
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2% 1%;
}

.about-title {
  grid-row: 1;
  grid-column: 1 / span 2;
  text-decoration: underline var(--mainGreen);
}

.about-photo {
  grid-row: 2;
  grid-column: 1; 
  margin-left: 4%;
}

.about-text {
  grid-row: 2;
  grid-column: 2;
  width: 95%;
  padding: 3% 0;
  font-size: 1.2rem;
}

/* Contact Styles */
.contact-section {
  background-color: var(--softGray);
  color: var(--darkGray);
}

.contact-title {
  text-decoration: underline var(--mainBlue);
}

.contact-info {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: center;
  align-content: stretch;
}

.contact-info img {
  width: 4vw;
  height: 10vh;
  padding: 0 0.5em;
}

.contact-info figcaption {
  text-align: center;
  color: var(--softGray);
  font-size: 1.1rem;
}

.contact-info a {
  transition: background-color 0.8s;
  text-decoration: underline var(--mainBlue);
}

.contact-info a:nth-child(even) {
  text-decoration: underline var(--mainGreen);
}

.contact-info a:hover {
  background-color: var(--mainBlue);
}

.contact-info a:hover:nth-child(even) {
  background-color: var(--mainGreen);
}

/* Media Queries */
@media (hover: none) {
  .contact-info figcaption {
    color: var(--darkGray);
  }
}


@media screen and (min-width: 1921px) {
  html {
    max-width: 1920px;
    margin-left: auto;
    margin-right: auto;
    font-size: 2rem;
  }

  .project-block {
    width: 100%;
  }

  .intro-icons img {
    width: 8.5vw;
  }

  .contact-info img {
    width: 5.5vw;
  }

  .cert-block {
    width: 20vw;
  }
}

@media screen and (max-width: 1201px) {
  html {
    font-size: 1.2rem;
  }

  .intro-icons img {
    width: 9.5vw;
  }

  .project-block {
    width: 90vw;
    height: 60vh;
  }

  .contact-info img {
    width: 5.5vw;
  }
}

@media screen and (max-width: 801px) {
  .section-title {
    font-size: 2rem;
  }

  .hero h1 {
    font-size: 3.5rem;
  }

  .break-line {
    width: 70vw;
  }

  .intro-icons img {
    width: 15vw;
  }

  .project-block {
    width: 90vw;
    height: 135vh;
    grid-template-columns: 1fr;
    grid-template-rows: 7% 50% 25% 7% 11%;
    grid-column-gap: 0;
  }

  .project-images {
    grid-row: 2;
  }

  .project-name {
    grid-column: 1;
    grid-row: 1;
    padding: 3% 0 5%;
  }

  .project-desc {
    grid-column: 1;
    grid-row: 3;
  }

  .project-tech {
    grid-column: 1;
    grid-row: 4;
  }

  .project-links {
    grid-column: 1;
    grid-row: 5;
  }

  .cert-block {
    width: 60vw;
  }

  .about-photo {
    margin-left: 7%;
  }

  .contact-info img {
    width: 10vw;
  }
}

@media screen and (max-width: 551px) {
  .hero {
    margin-top: 15vh;
  }

  .hero h1 {
    font-size: 4.75rem;
  }

  .break-line {
    width: 85vw;
  }

  .intro-icons img {
    width: 25vw;
  }

  .more {
    margin-top: 4%;
  }

  .more img {
    width: 6vw;
  }

  .contact-info img {
    padding: 0;
    width: 12vw;
  }
}